import React, { useEffect, useState } from 'react';
import "./home.css";
import Button from './Button'; // Composant de bouton rutilisable

const HomeData = () => {
    // Utilisation du hook useState pour grer l'tat scrollPercentage, initialis  0
    const [scrollPercentage, setScrollPercentage] = useState(0);

    // Utilisation du hook useEffect pour effectuer une action lors du dfilement de la page
    useEffect(() => {
        // Fonction handleScroll appele  chaque vnement de dfilement de la page
        const handleScroll = () => {
            const scrollHeight = document.documentElement.scrollHeight - window.innerHeight;
            const scrollTop = window.scrollY;
            const percentage = (scrollTop / scrollHeight) * 100;
            setScrollPercentage(percentage);
        };

        // Ajout d'un couteur d'vnements de dfilement  la fentre
        window.addEventListener('scroll', handleScroll);

        // Suppression de l'couteur d'vnements de dfilement lorsque le composant est dmont
        return () => {
            window.removeEventListener('scroll', handleScroll);
        };
    }, []); // La dpendance est un tableau vide, ce qui signifie que ce code ne s'excutera qu'une seule fois aprs le rendu initial

    // Rendu des lments HTML avec les donnes personnelles, un bouton de contact et un indicateur de dfilement
    return (
        <div className="home_data">
            <h1 className="home_title">
                M'Bissi Kongo Dervilon
            </h1>
            <h3 className="home_subtitle"> Product Owner </h3>
            <p className="home_description">
                Diplm en Manager stratgie et dveloppement de projet digital, 
                je suis passionn par l'alliance entre latechnologie et la stratgie de produits. 
                Fort d'une expertise technique solide et d'une vision stratgique, 
                je recherche activement des opportunits dans ce domaine.
            </p>
            {/* Utilisation du composant Button avec des proprits pour la personnalisation */}
            <Button href="#contact" buttonText="Contact" />

            {/* Indicateur de dfilement */}
            <div className="scroll-indicator-container">
                <div
                    className="scroll-indicator"
                    style={{ width: `${scrollPercentage}%` }} // La largeur de l'indicateur est dfinie en fonction du pourcentage de dfilement
                />
            </div>
        </div>
    );
}

export default HomeData;
